import React from 'react';

function CheckMark(props){
    return(
        <div className="checkmark">
            <img className="checkmark-img" src={`${process.env.PUBLIC_URL}/assets/tick.svg`} alt="checkmark" />
            <p className="checkmark-text ml-15">
                {props.text}
            </p>
        </div>
    )
}

export default CheckMark;